import React, { useState } from "react";
import "./Menu.css";

const menuLinks: { link: string; content: string }[] = [
  {
    link: "#aboutMe",
    content: "About Me",
  },
  {
    link: "#skills",
    content: "Skills",
  },
  {
    link: "#workAndEducation",
    content: "Work & Education",
  },
  // {
  //   link: "#projectsAndBlogs",
  //   content: "Projects & Blogs",
  // },
];

const Menu = () => {
  const [colorChange, setColorchange] = useState(false);
  let linkRefs: NodeListOf<HTMLAnchorElement> | undefined = undefined;
  let menuHeight: number = 0;
  const changeNavbarColor = () => {
    const fromTop = window.scrollY;

    if (fromTop >= 50) {
      setColorchange(true);
    } else {
      setColorchange(false);
    }

    if (!linkRefs) {
      linkRefs = document.querySelectorAll(".navLink");
    }
    if (menuHeight === 0) {
      const container = document.getElementById("navContainer");
      menuHeight = container?.offsetHeight ?? 0;
    }

    const offsetTop = fromTop + menuHeight;
    linkRefs.forEach((link) => {
      const section = document.querySelector(link.hash) as HTMLElement;
      if (
        section &&
        section.offsetTop <= offsetTop &&
        section.offsetTop + section.offsetHeight > offsetTop
      ) {
        link.classList.add("active");
      } else {
        link.classList.remove("active");
      }
    });
  };
  window.addEventListener("scroll", changeNavbarColor);
  return (
    <nav className={colorChange ? "scrolled" : ""}>
      <section id="navContainer">
        <a href="/#" className="navLogo">
          Mr - <span className="acentColor">Ashok</span>
        </a>
        <section>
          {menuLinks.map((linkData) => (
            <a href={linkData.link} className="navLink">
              {linkData.content}
            </a>
          ))}
        </section>
      </section>
    </nav>
  );
};
export default Menu;
